@import 'src/styles/media';

.root {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  row-gap: 0.5rem;

  @include tablet-portrait {
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  @include print {
    display: none;
  }

  .tools {
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 1rem;

    @include tablet-landscape {
      column-gap: 0.6rem;
    }

    .theme {
      @include tablet-portrait {
        position: absolute;
        top: 3.2rem;
      }
    }
  }

  .buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 1.5px;

    & > *:not(:first-child) {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    & > *:not(:last-child) {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }
}
